@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

:root {
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI",
    "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

.material-icons {
  font-family: "Material Icons";
  display: inline-block;
}

h1 {
  @apply text-5xl font-extrabold text-black;
}

h2 {
  @apply text-4xl font-bold text-black;
}

h3 {
  @apply text-3xl font-semibold text-black;
}

h4 {
  @apply text-2xl font-extrabold text-black;
}

h5 {
  @apply text-xl font-bold text-orange-600;
}

p {
  @apply text-xl font-medium text-black leading-relaxed;
}

.p1 {
  @apply font-bold;
}

.p2 {
  @apply font-light;
}

@media (max-width: 768px) {
  h1 {
    @apply text-4xl;
  }
  h2 {
    @apply text-3xl;
  }
  h3 {
    @apply text-2xl;
  }
}
